.navBtn {
    padding: 10px 20px;
    margin: 10px 15px 30px 15px;
    background-color: #CDE990;
    border: 2px solid #BFDB38;
    border-radius: 10px;
    color: #473C33;
}

.navBtn:hover {
    background-color: #AACB73;
}

label {
    color: #473C33;
    margin: 5px;
}

input {
    text-align: center;
}

#age {
    width: 30px;
    margin: 10px 0px;
}


.radioButton{
    accent-color: #AACB73;
}

.hewe {
    margin: 10px 5px;
    width: 50px;
}

.calcBtn {
    padding: 7px 40px;
    margin: 10px;
    background-color: #CEEDC7;
    border: 2px solid #E5FDD1;
    border-radius: 10px;
    color: #473C33;
}

.calcBtn:hover {
    background-color: #b9eead;
}

.result {
    /* margin: 10px 30%; */
    background-color: #f5ede7;

}

.Normal {
    color: green;
}

.Severe {
    color: red;
}

.Moderate {
    color: pink;
}

.Mild {
    color: yellow;
}

.Overweight {
    color: yellow;
}

.Obese {
    color: red;
}


.calculation {
    /* margin-right: 50%; */
    width: 100%;
    height: auto;
    border: 1px solid #AACB73;
}

.BMIinfo {
    /* margin-left: 50%; */
    border: 1px solid #AACB73;
}

.columnAll {
    /* column-width: 500px; */
    column-count: 2;
    column-gap: 50px;
    /* column-rule: 5px solid black; */



    display: flex;
}

.headerBMI {
    color: #AACB73;
    font-size: 50px;
    margin: 2px;
    

